<template>
  <div class="tutorial-container">
    <!--  -->
    <div class="cat-sidebar">
      <div
        class="cat-link"
        :class="{ active: selectedCategory === '' }"
        @click="selectedCategory = ''"
      >
         <span class="count">{{ countAll }}</span>
      </div>

      <div
        v-for="c in categories"
        :key="c"
        class="cat-link"
        :class="{ active: selectedCategory === c }"
        @click="selectedCategory = c"
      >
        {{ c }} <span class="count">{{ countByCategory(c) }}</span>
      </div>
    </div>

    <!--  -->
    <div class="tutorial-content">
      <div class="toolbar">
        <input
          v-model.trim="keyword"
          class="search"
          placeholder="GWAS / scRNA / ATAC / QC ..."
        />
        <div class="meta"> {{ filtered.length }} </div>
      </div>

      <div v-if="filtered.length === 0" class="empty">
        
      </div>

      <template v-else>
        <!--   -->
        <BoardQA
          v-if="selectedCategory !== '' && boardKey"
          :repo="issueRepo"
          :boardKey="boardKey"
          :boardTitle="selectedCategory"
        />

        <!--  -->
        <div class="grid">
          <div class="tcard" v-for="t in filtered" :key="t.id">
            <div class="tmeta">
              <div class="tdate">{{ t.updated }}</div>
              <span class="ttype" v-if="t.category">{{ t.category }}</span>
            </div>

            <div class="timage">
              <img :src="t.icon" :alt="t.title" />
            </div>

            <div class="ttitle">{{ t.title }}</div>
            <div class="tdesc">{{ t.desc }}</div>

            <div class="ttags" v-if="t.tags && t.tags.length">
              <span class="tag" v-for="tag in t.tags" :key="tag">{{ tag }}</span>
            </div>

            <div class="tfooter">
              <button class="read-more" type="button" @click="open(t)">
                Read more 
              </button>

              <button class="open-btn" type="button" @click="open(t)">
                {{ t.type === "md" ? "Open" : "Link" }}
              </button>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { tutorials } from "@/tutorials";
import BoardQA from "@/components/BoardQA.vue";

const CATEGORY_KEY = {
  : "statgen",
  : "singlecell",
  : "engineering"
};

export default {
  name: "TutorialIndexView",
  components: { BoardQA },
  data() {
    return {
      keyword: "",
      selectedCategory: "",
      tutorials,

      //   GitHub 
      issueRepo: "Crazzy-Rabbit/Wulab"
    };
  },
  computed: {
    categories() {
      return Array.from(new Set(this.tutorials.map((t) => t.category))).filter(Boolean);
    },
    filtered() {
      const kw = this.keyword.toLowerCase();
      return this.tutorials
        .filter((t) => (this.selectedCategory === "" ? true : t.category === this.selectedCategory))
        .filter((t) => {
          if (!kw) return true;
          const hay = [t.title, t.desc, t.category, (t.tags || []).join(" "), t.level]
            .join(" ")
            .toLowerCase();
          return hay.includes(kw);
        })
        .sort((a, b) => (a.updated < b.updated ? 1 : -1));
    },
    countAll() {
      if (!this.keyword) return this.tutorials.length;
      return this.filtered.length;
    },
    boardKey() {
      return CATEGORY_KEY[this.selectedCategory] || "";
    }
  },
  methods: {
    countByCategory(cat) {
      const kw = this.keyword.toLowerCase();
      return this.tutorials
        .filter((t) => t.category === cat)
        .filter((t) => {
          if (!kw) return true;
          const hay = [t.title, t.desc, t.category, (t.tags || []).join(" "), t.level]
            .join(" ")
            .toLowerCase();
          return hay.includes(kw);
        }).length;
    },
    open(t) {
      if (t.type === "md") {
        this.$router.push(`/tutorial/${t.slug}`);
      } else if (t.type === "external" && t.link) {
        window.open(t.link, "_blank", "noopener,noreferrer");
      }
    }
  }
};
</script>

<style scoped>
/*   */
.tutorial-container {
  --accent: #2f5d8a;
  --badgeBg: #f0f4fb;
  --badgeText: #1a3e6e;
  --border: #e3ebf5;
  --shadow: 0 4px 14px rgba(15, 60, 120, 0.08);
  --shadowHover: 0 16px 36px rgba(15, 60, 120, 0.16);
}

/*  +  */
.tutorial-container {
  display: flex;
  width: 100%;
  gap: 20px;
}

/*  */
.cat-sidebar {
  width: 200px;
  position: sticky;
  top: 30%;
  transform: translateY(-50%);
  align-self: flex-start;
}

.cat-link {
  display: flex;
  align-items: center;
  justify-content: space-between;

  font-size: 16px;
  font-weight: 700;
  color: #9aa4af;
  cursor: pointer;
  margin-bottom: 12px;
  transition: color 0.15s ease, background 0.15s ease;
  border-radius: 10px;
  padding: 8px 10px;
}

.cat-link:hover {
  color: var(--accent);
  background: rgba(47, 93, 138, 0.06);
}

.cat-link.active {
  color: var(--accent);
  background: rgba(47, 93, 138, 0.10);
}

.count {
  font-size: 12px;
  color: #666;
  border: 1px solid var(--border);
  background: #fff;
  padding: 2px 8px;
  border-radius: 999px;
  font-weight: 800;
}

.tip {
  margin-top: 12px;
  font-size: 12px;
  color: #888;
  line-height: 1.5;
}

/*  */
.tutorial-content {
  flex: 1;
  min-width: 0;
}

/*  */
.toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 14px;
}

.search {
  flex: 1;
  min-width: 260px;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid var(--border);
  outline: none;
}

.search:focus {
  border-color: rgba(47, 93, 138, 0.35);
  box-shadow: 0 6px 18px rgba(15, 60, 120, 0.10);
}

.meta {
  color: #666;
  font-weight: 800;
  white-space: nowrap;
}

/*  */
.grid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 22px;
  align-items: stretch;
}

/*  */
.tcard {
  background: #fff;
  border-radius: 16px;
  border: 1px solid var(--border);
  box-shadow: var(--shadow);
  overflow: hidden;
  padding: 14px 16px 56px 16px;
  position: relative;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.tcard:hover {
  transform: translateY(-6px);
  box-shadow: var(--shadowHover);
}

/*  +  */
.tmeta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 10px;
}

.tdate {
  font-size: 13px;
  font-weight: 900;
  color: var(--accent);
}

.ttype {
  flex: 0 0 auto;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 28px;
  padding: 0 10px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: rgba(47, 93, 138, 0.08);
  color: var(--accent);
  font-size: 12px;
  font-weight: 900;
  white-space: nowrap;
}

/* icon  */
.timage {
  width: 100%;
  height: 160px;
  border-radius: 12px;
  border: 1px solid var(--border);
  overflow: hidden;
  box-shadow: 0 2px 10px rgba(15, 60, 120, 0.08);
  background: #fff;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.timage img {
  width: 70%;
  height: 70%;
  object-fit: contain;
  display: block;
}

/*  */
.ttitle {
  font-size: 16px;
  font-weight: 900;
  color: var(--accent);
  line-height: 1.35;
  margin-bottom: 10px;
  word-break: break-word;
}

/*  */
.tdesc {
  font-size: 14px;
  color: #444;
  line-height: 1.75;
  word-break: break-word;
  overflow-wrap: anywhere;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  overflow: hidden;
}

/* tags */
.ttags {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tag {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 26px;
  padding: 0 10px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: #fff;
  color: #555;
  font-size: 12px;
  font-weight: 900;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/*  */
.tfooter {
  position: absolute;
  left: 16px;
  right: 16px;
  bottom: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.read-more,
.open-btn {
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  font-size: 14px;
  font-weight: 900;
  color: var(--accent);
}

.read-more:hover,
.open-btn:hover {
  text-decoration: underline;
}

/* empty */
.empty {
  padding: 26px;
  background: #fff;
  border-radius: 14px;
  border: 1px dashed var(--border);
  color: #777;
}

/* responsive */
@media (max-width: 1024px) {
  .grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 768px) {
  .cat-sidebar {
    display: none;
  }
  .grid {
    grid-template-columns: 1fr;
  }
}
</style>
